<template>
  <div class="detail">
    <el-table
      :data="arr"
      style="width: 90%;margin: 0 auto">
      <el-table-column
        type="index"
        label="#"
        width="50">
      </el-table-column>
      <el-table-column
        prop="userName"
        label="用户名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="blong"
        label="用户名"
        width="220">
      </el-table-column>
      <el-table-column
        label="状态"
        width="120">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.state">通过</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="reason"
        label="原因"
        width="200">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="look(scope.row)">查看
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%">
      <img :src="item.pic" alt="" class="img">
      <div class="wrp">
        <el-badge :value="a" class="item">
          <el-button size="small" type="success">优</el-button>
        </el-badge>
        <el-badge :value="b" class="item">
          <el-button size="small" type="info">良</el-button>
        </el-badge>
        <el-badge :value="c" class="item">
          <el-button size="small" type="warning">中</el-button>
        </el-badge>
        <el-badge :value="d" class="item">
          <el-button size="small" type="danger">差</el-button>
        </el-badge>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false" class="btn1">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false" class="btn2">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "detail",
    data() {
      return {
        arr: [],
        dialogVisible: false,
        item: '',
        a: 0,
        b: 0,
        c: 0,
        d: 0,
      }
    },
    methods: {//查看
      look(val) {
        this.a = 0;
        this.b = 0;
        this.c = 0;
        this.d = 0;
        for (let i = 0; i < val.common.length; i++) {
          console.log(1111);
          if (val.common[i].content === 0) {
            this.a++;
          } else if (val.common[i].content === 1) {
            this.b++;
          } else if (val.common[i].content === 2) {
            this.c++;
          } else {
            this.d++;
          }
        }
        this.item = val;
        this.dialogVisible = true;
      },
      getMsg() {
        let discussId = this.$route.query.id;
        let params = {discussId};
        this.$axios.post('getSummarizes', params).then(res => {
          console.log(res);
          this.arr = res.data;
        });
      }
    },
    created() {
      this.getMsg()
    }
  }
</script>

<style scoped>
  .img {
    display: block;
    margin: 0 auto;
    width: 300px;
    height: 300px;
  }

  .btn1 {
    float: left;
  }

  .btn2 {
    float: right;
  }

  .wrp {
    margin-top: 20px;
  }

  .item {
    float: left;
    margin-left: 40px;
  }
</style>
<style>
  .detail .el-dialog__footer {
    width: 40%;
    height: 40px;
    box-sizing: content-box;
    margin: 0 auto;
  }
</style>



